.button-hover {
	background: var(--iq-primary-hover) !important;
	border: none;
	color: var(--iq-white);
	transition: color 0.3s ease;
	display: inline-block;
	vertical-align: middle;
	-webkit-transform: perspective(1px) translateZ(0);
	transform: perspective(1px) translateZ(0);
	box-shadow: 0 0 1px rgba(0, 0, 0, 0);
	position: relative;
	border-radius: 5px;
	background: var(--iq-primary-hover);
}
.swiper-slide .block-images {
	overflow: visible;
	transition: all 0.5s ease-in-out;
}
.swiper-slide:hover .block-images {
	overflow: visible;
	cursor: pointer;
	z-index: 999;
	box-shadow: 0px 0px 21px 1px #f72424d1 !important;
}
.iq-custom-select1 {
	width: 150px;
	border: 1px solid black;
	position: absolute;
}

.swiper-slide .block-images::before {
	position: absolute;
	content: '';
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: rgba(0, 0, 0, 0.8);
	width: 100%;
	height: 100%;
	opacity: 0;
}
.swiper-slide:hover .block-images::before {
	opacity: 1;
	z-index: 9;
}
.swiper-slide:hover .block-images {
	overflow: visible;
}
.swiper-slide:hover .block-images .img-box {
	position: relative;
}
.swiper-slide:hover .block-images {
	box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.9);
}
.block-social-info {
	position: absolute;
	top: 0;
	left: auto;
	bottom: 0;
	right: 25px;
	z-index: 999;
	display: flex;
	align-items: center;
}
.music-play-lists li {
	position: relative;
	height: 30px;
	width: 30px;
	line-height: 35px;
	text-align: center;
	background: rgba(255, 255, 255, 0.3);
	border-radius: 50%;
	margin: 0 auto 7px;
	display: flex;
	-webkit-display: flex;
	-moz-display: flex;
	-o-display: flex;
	align-items: center;
}
.music-play-lists span {
	position: relative;
	display: block;
	height: 20px;
	width: 20px;
	line-height: 26px;
	font-size: 12px;
	text-align: center;
	background: var(--iq-white);
	color: var(--iq-primary);
	border-radius: 50%;
	-webkit-border-radius: 50%;
	-moz-border-radius: 50%;
	-o-border-radius: 50%;
	margin: 0 auto;
	display: flex;
	-webkit-display: flex;
	-moz-display: flex;
	-o-display: flex;
	align-items: center;
	justify-content: center;
	transition: all 0.45s ease 0s;
	-webkit-transition: all 0.45s ease 0s;
	-moz-transition: all 0.45s ease 0s;
	-o-transition: all 0.45s ease 0s;
	cursor: pointer;
}

.music-play-lists .count-box {
	height: 15px !important;
	width: 15px !important;
	line-height: 15px i !important;
	font-size: 8px !important;
	background: var(--iq-primary) !important;
	color: var(--iq-white-color) !important;
	position: absolute;
	right: 0;
	top: 0px;
	padding: 0;
	text-align: center !important;
}

.music-play-lists li:hover span {
	background: var(--iq-primary);
	color: var(--iq-white);
	transition: all 0.45s ease 0s;
	-webkit-transition: all 0.45s ease 0s;
	-moz-transition: all 0.45s ease 0s;
	-o-transition: all 0.45s ease 0s;
}
.block-description {
	position: absolute;
	left: 25px;
	top: 0;
	bottom: 0;
	z-index: 999;
	display: flex;
	justify-content: center;
	flex-direction: column;
}
.swiper-slide:hover .block-social-info {
	animation: fadeIn 0.6s ease-in-out;
	opacity: 1;
}
