@media (min-width: 1200px) {
	li.slide-item.slick-active
		+ li.slide-item.slick-active
		+ li.slide-item.slick-active
		+ li.slide-item.slick-active:hover
		.block-images {
		transform: scale3d(1.1, 1.1, 1) translate3d(-6%, 0, 0) perspective(500px);
	}
	.watch-title {
		position: absolute;
		top: 50%;
		left: -20%;
		transform: translateY(-50%);
		font-size: 17px;
		letter-spacing: 3.5px;
		font-weight: 600;
		color: var(--iq-white);
	}
	.watch-title:before {
		content: '';
		position: absolute;
		top: 50%;
		left: -36%;
		transform: translateY(-50%);
		background: var(--iq-white);
		width: 52px;
		height: 2px;
	}
	.col-1-5 {
		-ms-flex: 0 0 33.3%;
		flex: 0 0 33.3%;
		max-width: 33.3%;
	}
}
