header#main-header {
	position: fixed;
	left: 0px;
	right: 0px;
	text-align: center;
	z-index: 99;
	background: rgba(20, 20, 20, 0.5) !important;
}
header .navbar-light .navbar-brand img.logo {
	width: 160px;
}
.iq-search-bar .search-input {
	width: 100%;
	height: 40px;
	padding: 5px 15px 5px 40px;
	border: none;
	border-radius: 0;
	color: var(--iq-white);
	background: var(--iq-bg1);
}
.iq-search-bar .searchbox .search-link {
	position: absolute;
	left: 15px;
	top: 6px;
	font-size: 16px;
}
.nav-item span.dots {
	height: 10px;
	width: 10px;
	font-size: 0;
	text-align: center;
	padding: 0;
	position: absolute;
	top: 3px;
	right: 12px;
	border-radius: 50%;
	-webkit-border-radius: 50%;
}
.menu-right .nav-item span.dots {
	right: 0;
}
.navbar-right li .iq-sub-dropdown .iq-sub-card {
	position: relative;
	font-size: inherit;
	padding: 15px 15px;
	line-height: normal;
	color: inherit;
	text-align: left;
	display: inline-block;
	width: 100%;
	border: 1px solid;
	border-image-slice: 1;
	border-width: 0 0 1px 0;
	border-image-source: linear-gradient(
		to right,
		rgba(209, 208, 207, 0.6),
		rgba(209, 208, 207, 0.3),
		rgba(209, 208, 207, 0)
	);
}
.iq-sub-card.setting-dropdown {
	padding: 15px 15px 15px 0 !important;
}
.navbar-right li .iq-sub-dropdown a.iq-sub-card:last-child {
	border-bottom: 0;
}
.notify-bg {
	overflow: hidden;
	z-index: 99;
}
.notify-bg:before {
	content: '';
	display: block;
	position: absolute;
	background: rgba(255, 255, 255, 0.5);
	width: 70px;
	height: 100%;
	left: 0;
	top: 0;
	opacity: 0.5;
	filter: blur(30px);
	transform: translateX(-100px) skewX(-30deg);
	animation: noti-before-animation 2s infinite 2s;
}
.notify-bg:after {
	content: '';
	display: block;
	position: absolute;
	background: rgba(255, 255, 255, 0.2);
	width: 30px;
	height: 100%;
	left: 30px;
	top: 0;
	opacity: 0;
	filter: blur(5px);
	transform: translateX(-100px) skewX(-30deg);
	animation: noti-after-animation 2s infinite 2s;
}
.navbar-right .iq-sub-dropdown .iq-card-body div {
	border-radius: 0;
}
.navbar-right .iq-show .iq-sub-dropdown,
.iq-show .search-box {
	opacity: 1;
	visibility: visible;
	transform: translate(0, 0);
}
.navbar-right .iq-sub-dropdown {
	width: 300px;
	opacity: 0;
	visibility: hidden;
	position: absolute;
	top: 100%;
	right: 15px;
	background: var(--iq-bg1);
	transform: translate(0, 70px);
	-webkit-transform: translate(0, 70px);
	transition: all 0.3s;
	padding: 0;
	-webkit-box-shadow: 0px 0 20px 0 rgba(0, 0, 0, 0.5);
	-moz-box-shadow: 0px 0 20px 0 rgba(0, 0, 0, 0.5);
	box-shadow: 0px 0 20px 0 rgba(0, 0, 0, 0.5);
	-webkit-transition: all 0.3s ease-out 0s;
	-moz-transition: all 0.3s ease-out 0s;
	-ms-transition: all 0.3s ease-out 0s;
	-o-transition: all 0.3s ease-out 0s;
	transition: all 0.3s ease-out 0s;
	z-index: 1;
}
.navbar-light .navbar-toggler-icon {
	background: url(../../assets/images/menu.png) no-repeat scroll center center;
}
.search-toggle:hover,
header .navbar ul li.menu-item a:hover {
	color: var(--iq-primary) !important;
}
.search-wrapper {
	position: relative;
