	bottom: -2px;
}
.search::placehoder {
	color: var(--iq-white) !important;
}
.noti-svg {
	-webkit-animation: notify 1.5s ease infinite;
	animation: notify 1.5s ease infinite;
	fill: var(--iq-body-text);
}
.noti-svg:hover {
	fill: var(--iq-primary);
}
.search-box {
	position: absolute;
	left: 0;
	right: 0;
