	color: var(--iq-primary);
}
.navbar ul.navbar-nav > li.menu-item > a {
	text-transform: uppercase;
}
.navbar ul.navbar-nav > li:hover > a {
	font-weight: 700;
}
.navbar ul li.menu-item a {
	font-size: 16px;
	font-weight: 400;
	position: relative;
	padding: 0px 0px 0px 10px;
	line-height: 70px;
	position: relative;
	z-index: 9;
	text-decoration: none;
	transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	text-transform: capitalize;
}
.navbar ul li .sub-menu {
	display: none;
}
/* .navbar ul {
    float: inherit;
    display: block;
    text-align: center;
} */
.navbar ul li .sub-menu li {
	margin-right: 0;
	padding: 0 15px;
}
.navbar ul.sub-menu li {
	display: block;
	text-align: left;
}
.navbar ul li {
	list-style: none;
	margin-right: 18px;
	position: relative;
	transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	display: inline-block;
}
.menu-sticky .navbar ul li .sub-menu > li a {
	line-height: 1em;
}
.navbar ul li .sub-menu li > a {
	font-size: 14px;
	border-radius: 0;
	display: inline-block;
	width: 100%;
	line-height: 1em;
	padding: 15px;
	color: var(--iq-body-text);
	position: relative;
	overflow: hidden;
}
.navbar ul li.menu-item a {
	font-size: 16px;
	font-weight: 400;
	position: relative;
	padding: 0px 0px 0px 10px;
	line-height: 70px;
	position: relative;
	z-index: 9;
	text-decoration: none;
	transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
	-moz-transition: all 0.3s ease-in-out;
	-ms-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	-webkit-transition: all 0.3s ease-in-out;
	text-transform: capitalize;
}
.navbar ul li .sub-menu li > a::before {
	position: absolute;
	content: '';
	background: var(--iq-primary);
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 4px;
	opacity: 0;
	transform: translate3d(-100%, 0, 0);
	transition: all 0.5s ease-in-out;
}
header .navbar ul li .sub-menu li.current-menu-parent > a::after,
header .navbar ul li .sub-menu li .sub-menu li.menu-item.current-menu-item > a::after,
header .navbar ul li .sub-menu li .sub-menu li.menu-item:hover > a::after {
	opacity: 1;
	transform: translate3d(0, 0, 0);
	transition: all 0.5s ease-in-out;
}
header .navbar ul li .sub-menu li:hover > a::after {
	opacity: 1;
	transform: translate3d(0, 0, 0);
	transition: all 0.5s ease-in-out;
}
.navbar ul li .sub-menu li > a::after {
	position: absolute;
	content: '';
	background: transparent linear-gradient(270deg, var(--iq-form-gradient-color) 0%, var(--iq-to-gradient-color) 100%);
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	opacity: 0;
	transform: translate3d(-100%, 0, 0);
	transition: all 0.5s ease-in-out;
	z-index: -1;
}
header .navbar ul li .sub-menu li.current-menu-parent > a::after,
header .navbar ul li .sub-menu li .sub-menu li.menu-item.current-menu-item > a::after,
header .navbar ul li .sub-menu li .sub-menu li.menu-item:hover > a::after {
	opacity: 1;
	transform: translate3d(0, 0, 0);
	transition: all 0.5s ease-in-out;
}
header .navbar ul li .sub-menu li:hover > a::before {
	opacity: 1;
	transform: translate3d(0, 0, 0);
	transition: all 0.5s ease-in-out;
}
/* RTL BOX */
.rtl-box {
	position: fixed;
	top: 115px;
	right: 0px;
	background-color: var(--white);
	z-index: 1000;
	transition: all 500ms ease-in-out;
	transform: translateX(100%);
}
.rtl-box.show {
	transition: all 500ms ease-in-out;
	transform: translateX(0);
}
.rtl-btn {
	position: absolute;
	left: -35px;
	padding: 0em;
	background-color: var(--dark);
	border: 2px solid var(--dark);
}
.rtl-btn:focus {
	outline: none;
	box-shadow: none;
}
.modes {
	margin: 0;
	padding: 0.938em;
}
.modes li {
	cursor: pointer;
	color: var(--iq-secondary);
	display: block;
	border: 1px solid rgb(82, 79, 79);
	text-align: center;
	padding: 0.313em 1.5em;
}
.modes li {
	list-style: none;
}
.dir-btn.active {
	background: var(--dark);
	color: var(--white);
}

/* blog */
.widget {
	margin-bottom: 1.875em;
	padding: 1.875em;
	display: inline-block;
	width: 100%;
	position: relative;
	background: linear-gradient(180deg, rgb(25 25 25) 0%, rgb(25 25 25) 10%, rgba(83, 100, 141, 0) 100%);
	border-top: 0.25em solid rgba(209, 208, 207, 0.6);
}
label {
	color: gray;
}
.widget .search-form input {
	background: rgba(209, 208, 207, 0.6);
}
.screen-reader-text {
	position: absolute !important;
	clip: rect(1px, 1px, 1px, 1px);
	overflow: hidden;
	height: 1px;
	width: 1px;
	display: none;
}
.search-form {
	position: relative;
}
.search-form label {
	display: none;
}
.search-form input {
	padding-right: 2.5em;
	box-shadow: none;
	border-radius: 0;
	color: var(--iq-body-text);
	border: 1px solid rgba(36, 38, 43, 0.14);
	background: var(--iq-bg1);
	transition: all 0.3s ease-in-out;
	border-radius: 0px;
}
.search-form .search-submit {
	position: absolute;
	right: 0;
	top: 0;
	color: var(--iq-white-color);
	cursor: pointer;
	font-size: 18px;
	background: var(--iq-primary);
	border: none;
	outline: none;
	box-shadow: none;
	transition: all 0.3s ease-in-out;
	height: 48px;
	width: 48px;
}
.widget .widget-title {
	font-size: 1.375em;
	position: relative;
	margin-bottom: 1.364em;
	padding-bottom: 0.682em;
	border: 0.045em solid;
	border-image-slice: 1;
	border-width: 0 0 0.045em 0;
	border-image-source: linear-gradient(
		to right,
		rgba(209, 208, 207, 0.6),
		rgba(209, 208, 207, 0.3),
		rgba(209, 208, 207, 0)
	);
	margin-top: 0;
}
.post-img-holder {
	width: 80px;
	margin-right: 20px;
	flex: none;
	line-height: 0;
}
.post-img-holder a {
	display: block;
	width: 100%;
	height: 70px;
	overflow: hidden;
	background-position: center bottom;
	background-size: cover;
	background-repeat: no-repeat;
}
.iq-widget-menu .post-blog {
	line-height: 0px;
}
.iq-widget-menu .post-img .post-blog .blog-box ul li {
	margin-bottom: 0px;
}
.widget ul li:last-child {
	padding-bottom: 0;
}
.widget_categories ul li,
.widget_archive ul li,
.widget_pages ul li {
	margin-bottom: 15px;
	border-bottom: 1px solid rgba(82, 95, 129, 0.1);
}
.widget ul {
	padding: 0;
	margin: 0;
}
.iq-widget-menu .iq-post li a.date-widget {
	color: var(--iq-primary);
	margin-bottom: 10px;
	display: block;
	font-size: 12px;
	font-weight: 700;
}
.iq-widget-menu ul.iq-post li {
	margin-bottom: 24px;
}
.iq-widget-menu ul.iq-post li .post-img {
	display: flex;
}
.list-inline {
	padding-left: 0;
	list-style: none;
}
.widget ul li {
	padding-bottom: 15px;
	list-style: none;
	margin-bottom: 15px;
	border-bottom: 0.063em solid rgba(82, 95, 129, 0.1);
}
.widget_tag_cloud ul {
	margin: 0;
	padding: 0;
}
.widget_tag_cloud ul li {
	padding: 0;
	display: inline-block;
	margin: 3px 5px 3px 0;
}
.widget_tag_cloud ul li a {
	background: var(--iq-primary);
	display: inline-block;
	color: var(--iq-white-color);
	padding: 2px 10px;
}
.iq-blog-box {
	position: relative;
	overflow: hidden;
	margin-bottom: 3.125em;
	padding-bottom: 2.5em;
	border: 1px solid;
	border-image-slice: 1;
	border-width: 0 0 0.063em 0;
	border-image-source: linear-gradient(
		to right,
		rgba(209, 208, 207, 0.6),
		rgba(209, 208, 207, 0.3),
		rgba(209, 208, 207, 0)
	);
}
.iq-blog-box .iq-blog-image {
	position: relative;
	text-align: center;
	display: inline-block;
	width: 100%;
}
.iq-blog-box .iq-blog-image img {
	margin-bottom: 1.875em;
	background: var(--iq-white-light-color);
}
img {
	max-width: 100%;
	height: auto;
}
.has-post-thumbnail .iq-blog-box .iq-blog-detail {
	display: inline-block;
	float: left;
	width: 100%;
}
.iq-blog-box .iq-blog-detail .iq-blog-meta ul {
	margin: 0 0 1.25em;
	padding: 0;
	line-height: 1em;
}
.iq-blog-meta ul li {
	color: var(--iq-white-color);
	text-transform: uppercase;
	font-style: italic;
	margin: 0 0.625em 0.625em 0;
	font-size: 0.875em;
	border-left: 0.25em solid var(--iq-primary);
	background: transparent linear-gradient(270deg, var(--iq-form-gradient-color) 0%, var(--iq-to-gradient-color) 100%);
	padding: 0.625em 0.625em 0.625em 0.938em;
	position: relative;
	overflow: hidden;
	display: inline-block;
}
.blog-button .button-link,
.comment-respond .form-submit .submit,
.commentlist .iq-comments-media .reply a {
	background: var(--iq-primary-hover);
	color: var(--iq-white-color);
	display: inline-block;
	vertical-align: middle;
	padding: 10px 10px;
	font-size: 14px;
	line-height: 1.5;
	text-transform: uppercase;
	letter-spacing: 0px;
}
.iq-blog-box .iq-blogcat {
	padding: 0;
	display: inline-block;
	width: 100%;
	position: static;
	margin: 1.875em 0 0;
}
.iq-blog-box .iq-blogcat li {
	list-style: none;
	float: left;
	margin: 0 0.625em 0.625em 0;
}
.iq-blog-box .iq-blogcat li a {
	background: rgba(41, 41, 41, 0.76);
	color: var(--iq-body-text);
	padding: 0.313em 0.625em;
	text-transform: uppercase;
	font-size: 0.875em;
}
.iq-blog-box .iq-blogtag {
	padding: 0;
	display: inline-block;
	width: 100%;
	margin-top: 0px;
	margin-bottom: 0;
}
.iq-blog-box .iq-blogtag li {
	list-style: none;
	float: left;
	margin: 0 0.625em 0.625em 0;
}
.iq-blog-box .iq-blogtag li a {
	background: rgba(41, 41, 41, 0.76);
	color: var(--iq-body-text);
	padding: 0.313em 0.625em;
	text-transform: uppercase;
	font-size: 0.875em;
}

/* blog-details */
blockquote,
.wp-block-quote.is-style-large {
	background: rgba(83, 81, 79, 0.4);
	padding: 15px 30px;
	border-left: 4px solid var(--iq-primary);
	margin-bottom: 30px;
	border-radius: 8px;
}
blockquote cite {
	font-family: 'Heebo', sans-serif;
	font-weight: bold;
}
cite a {
	color: var(--iq-primary);
}
.comments-area,
.comment-respond {
	margin-top: 45px;
	clear: both;
}
.comments-area .comments-title,
.comment-respond .comment-reply-title {
	position: relative;
	padding-bottom: 0px;
}
form {
	display: block;
	margin-top: 0em;
}
.comment-respond .comment-form-comment {
	margin: 30px 0;
	margin-bottom: 30px;
	display: inline-block;
	width: 100%;
}
.comment-form-author,
.comment-form-email,
.comment-form-url {
	margin-bottom: 30px;
	display: block;
	width: 100%;
}
.comment-respond textarea,
.comment-respond input {
	float: inherit;
}
/* p.comment-form-cookies-consent {display: flex;width: 100%; align-items: start; margin: 0px 0 30px 0;} */
input[type='checkbox'] {
	width: auto;
	margin-right: 10px;
	height: 25px;
	display: inline-block;
}
label {
	color: var(--iq-secondary);
	margin-bottom: 0;
}
.comment-respond .form-submit {
	display: inline-block;
	margin-bottom: 0;
}
.blog-button .button-link,
.comment-respond .form-submit .submit,
.commentlist .iq-comments-media .reply a {
	background: var(--iq-primary-hover);
	color: var(--iq-white-color);
	border: none;
	position: relative;
	z-index: 9;
	display: inline-block;
	font-weight: 400;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	padding: 10px 10px;
	font-size: 14px;
	line-height: 1.5;
	text-transform: uppercase;
	letter-spacing: 0px;
}
embed,
iframe,
object {
	max-width: 100%;
	width: 100%;
}
